/* -----------------------------------------------------------------------------
  Default documentation page
----------------------------------------------------------------------------- */
/* -----------------------------------------------------------------------------
  Variables (inc)
----------------------------------------------------------------------------- */
/* -- Device screens variables ---------------------------------------------------------------------------- */
/* -- Color variables ------------------------------------------------------------------------------------ */
/* -- Theme elements fixed dimensions ------------------------------------------------------------------------------------ */
/* -- Others ------------------------------------------------------------------------------------ */
/* -----------------------------------------------------------------------------
  Mixins & functions (inc)
----------------------------------------------------------------------------- */
/* -- Device screens mixins ---------------------------------------------------------------------------- */
/* -- Sidebar ---------------------------------------------------------------------------- */
/* -----------------------------------------------------------------------------
  Root (inc)
----------------------------------------------------------------------------- */
body {
  position: relative;
  margin-top: 77px;
  font-family: Manrope, sans-serif;
  font-size: 16px;
  font-weight: 500;
}

#icon-sprite {
  display: none;
}

.inner-body {
  position: relative;
  width: 100%;
}

main {
  padding-left: 30px;
  padding-right: 15px;
  min-height: calc(100vh - 77px - 60px - 70px);
}

main img {
  max-width: 100%;
  margin-bottom: 1rem;
}

main p {
  font-family: Manrope, sans-serif;
  font-style: normal;
  letter-spacing: 0.03em;
}

main table {
  margin-bottom: 1rem;
}

main table td,
main table th {
  vertical-align: baseline;
}

.sidebar {
  flex-shrink: 0;
}

.sidebar .sidebar-container {
  top: 0;
  overflow-y: auto;
}

a {
  color: #256bd1;
}

a:hover, a:focus {
  color: #3585f9;
}

a:active {
  color: #256bd1;
  font-weight: 700;
}

th p {
  margin: 0;
  font-weight: inherit;
  font-weight: 500;
}

/* -----------------------------------------------------------------------------
  Defaults for content (inc)

  General complete style specifically required for the content, including
  lists, tables, etc. that is not necessarily need in all pages.
----------------------------------------------------------------------------- */
/* Fixes the anchor */
.section {
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.section::before {
  content: '';
  display: block;
  position: relative;
  width: 0;
  height: 1rem;
  margin-top: -1rem;
}

/* -----------------------------------------------------------------------------
  PART: HEADER

  Style for the header/top bar of the page.
----------------------------------------------------------------------------- */
/* -----------------------------------------------------------------------------
  Component: Brand and logo

  Style for the element containing the logo in the header.
----------------------------------------------------------------------------- */
.navbar-brand {
  display: flex;
  justify-content: flex-end;
}

.navbar-brand .logo-link.web {
  padding-bottom: .3rem;
}

.navbar-brand .logo-link.docs {
  margin-left: 5px;
  line-height: 2.15;
  color: #292929;
}

.navbar-brand .logo-link.docs:hover {
  text-decoration: none;
}

/* -----------------------------------------------------------------------------
  Component: Navigation bar

  Style for the menu with links that are external to the documentation (the
  menu in the top bar).
----------------------------------------------------------------------------- */
.navbar-nav .nav-link {
  padding: 0 10px;
  font-size: 18px;
  color: #292929;
}

.navbar-nav .nav-link:hover, .navbar-nav .nav-link:focus {
  fill: #256bd1;
  color: #256bd1;
  text-decoration: none;
}

.navbar-nav .nav-link:active {
  font-weight: 500;
}

.navbar-nav .nav-link.dropdown-toggle {
  padding-right: 27px;
}

.navbar-nav .nav-link.dropdown-toggle[aria-expanded="true"] {
  text-decoration: none;
}

.navbar-nav .nav-link.dropdown-toggle[aria-expanded="true"]::after {
  transform: rotateX(180deg);
  transition: transform ease-in .1s;
}

.navbar-nav .nav-link.dropdown-toggle::after {
  content: '';
  display: inline-block;
  width: 13px;
  height: 8px;
  background: url("../../images/icons/svg-icons.svg#menu-chevron");
  background-size: contain;
  background-position: center;
  position: absolute;
  right: 8px;
  bottom: .7rem;
  border: none;
  transition: transform ease-in .1s;
}

.navbar-nav .dropdown-menu {
  min-width: unset;
  padding: 15px;
  margin-top: 20px;
  border: 0;
  border-radius: 10px;
  box-shadow: 0 -2px 8px rgba(0, 0, 0, 0.05);
}

.navbar-nav .dropdown-menu::before {
  position: absolute;
  top: -9px;
  width: 18px;
  height: 18px;
  content: "";
  background-color: #fff;
  box-shadow: 0 -2px 8px rgba(0, 0, 0, 0.05);
  clip-path: inset(-15px 0 0 -15px);
  transform: rotate(45deg);
}

.navbar-nav .dropdown-menu .dropdown-item {
  padding: .25rem .75rem;
}

/* -----------------------------------------------------------------------------
  Component: Version selector

  Style for the release version selector.
----------------------------------------------------------------------------- */
#version-selector.show .dropdown-toggle::after {
  transform: rotate(180deg);
}

#version-selector .dropdown-btn {
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: calc((385px + 32px) / 2);
  height: 40px;
  background-color: #fff;
  border: 2px solid #fedd0b;
  border-radius: 10px;
}

#version-selector .dropdown-btn[aria-expanded="false"]:focus {
  box-shadow: inset 0 0 2px 2px rgba(101, 124, 151, 0.2);
}

#version-selector .dropdown-btn:focus {
  box-shadow: none;
}

#version-selector .dropdown-menu {
  top: 52px;
  box-sizing: border-box;
  width: 100%;
  border: 2px solid #fedd0b;
  border-radius: 10px;
}

#version-selector .dropdown-menu > li > a {
  display: inline-block;
  width: 100%;
  padding: .16rem .75rem;
  color: #292929;
  text-decoration: none;
}

#version-selector .dropdown-menu > li > a:hover, #version-selector .dropdown-menu > li > a:focus {
  font-weight: 700;
  background-color: #ebf2fb;
}

header {
  height: 77px;
  background: #fff;
  box-shadow: 0 4px 12px 0 rgba(53, 133, 249, 0.15);
}

header .navbar {
  height: 77px;
}

/* -----------------------------------------------------------------------------
  PART: Footer

  Style for the footer under the column containing the element main and the
  right sidebar.
----------------------------------------------------------------------------- */
footer {
  position: absolute;
  right: 0;
  left: 385px;
  height: 70px;
  padding: 15px 15px 15px 30px;
  background: #f6f6f6;
}

footer ul {
  padding-left: 15px;
  margin: 0;
}

footer .nav-link {
  padding: 0 10px;
  color: #292929;
}

footer .nav-link:hover {
  color: #3585f9;
}

/* -----------------------------------------------------------------------------
  PART: LEFT SIDEBAR

  Style for the left sidebar, which contains the global TOC.
----------------------------------------------------------------------------- */
/* -----------------------------------------------------------------------------
  Component: Search box

  Style for the searchbox in general.
----------------------------------------------------------------------------- */
.search-box {
  background-color: #ebf2fb;
  border: 2px solid #c1ccd9;
  border-radius: 10px;
}

.search-box .search-form {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
  height: 46px;
}

.search-box .search-form .form-control {
  position: relative;
  z-index: 1;
  width: 100%;
  font-size: 18px;
  font-weight: 400;
  background-color: #ebf2fb;
  border: none;
}

.search-box .search-form .btn-search {
  position: absolute;
  right: 0;
  z-index: 10;
  width: 46px;
  height: 46px;
  padding: 5px;
  background: url("../../images/icons/svg-icons.svg#search");
  background-size: 41px 41px;
  background-position: center;
  border: none;
}

/* -----------------------------------------------------------------------------
  Component: Global TOC

  Style for the menu containing the full index of the documentation release
  folder.
  Note: the elements of the global TOC are automatically generated by the html
  builder.
----------------------------------------------------------------------------- */
#global-toc ul {
  padding: 0;
  list-style: none;
}

#global-toc ul .current-toc-node {
  color: #256bd1;
}

#global-toc ul .current-toc-node:hover {
  color: #3585f9;
}

#global-toc ul .current-toc-node::before {
  position: absolute;
  top: 1.1rem;
  display: inline-block;
  width: 6px;
  height: 6px;
  pointer-events: none;
  content: "";
  border: 5px solid #256bd1;
  border-right-color: transparent;
  border-bottom: 0;
  border-left-color: transparent;
  transform: rotate(-90deg);
}

#global-toc .show > ul {
  display: block;
}

#global-toc li > ul {
  display: none;
}

#global-toc a {
  position: relative;
  display: inline-block;
  width: 100%;
  padding: 5px 0;
  font-size: 18px;
  font-weight: 400;
  line-height: 32px;
  color: #292929;
  text-decoration: none;
}

#global-toc a:hover {
  color: #3585f9;
}

#global-toc .current .toctree-l2 > a {
  padding-left: 20px;
}

#global-toc .current .toctree-l2 > a.current-toc-node::before {
  left: 5px;
}

#global-toc .current .toctree-l3 > a {
  padding-left: 40px;
}

#global-toc .current .toctree-l3 > a.current-toc-node::before {
  left: 25px;
}

#global-toc .current .toctree-l4 > a {
  padding-left: 60px;
}

#global-toc .current .toctree-l4 > a.current-toc-node::before {
  left: 45px;
}

#global-toc .current .toctree-l5 > a {
  padding-left: 80px;
}

#global-toc .current .toctree-l5 > a.current-toc-node::before {
  left: 65px;
}

#global-toc .current .toctree-l6 > a {
  padding-left: 100px;
}

#global-toc .current .toctree-l6 > a.current-toc-node::before {
  left: 85px;
}

#global-toc [class*="toctree-l"].current > a {
  font-weight: 700;
}

#global-toc [class*="toctree-l"].current.show > ul {
  display: block;
}

#global-toc [class*="toctree-l"] > ul {
  display: none;
}

#global-toc .toctree-l1 > a {
  padding-left: 40px;
}

#global-toc .toctree-l1 > a::before {
  left: 25px;
}

#global-toc .toctree-l1.show > a {
  background-color: #dcebff;
}

#global-toc .toctree-l1 > ul {
  margin-left: 40px;
  border-left: 1px solid #256bd1;
}

#left-sidebar {
  position: fixed;
  top: 77px;
  flex-basis: 385px;
  flex-shrink: 0;
  flex-grow: 0;
  width: 385px;
}

@media (min-width: 992px) {
  #left-sidebar {
    display: block;
  }
}

#left-sidebar .sidebar-container {
  width: 385px;
  height: calc(100vh - 77px);
  padding: 30px 0;
  background-color: #ebf2fb;
}

#left-sidebar .sidebar-container > nav {
  margin-top: 20px;
}

#left-sidebar .sidebar-container .search-box {
  margin-right: 40px;
  margin-left: 40px;
}

/* -----------------------------------------------------------------------------
  PART: CONTENT COLUMN

  Style for the content column containing the element main.
----------------------------------------------------------------------------- */
/* -----------------------------------------------------------------------------
  Component: accordions

  Style for the accordion sections.
----------------------------------------------------------------------------- */
.accordion-title {
  display: flex;
  width: 100%;
  align-items: center;
  border-bottom: 2px solid #b7c6d7;
  height: 1.5em;
}

.accordion-title::before {
  display: inline-block;
  width: 20px;
  height: 20px;
  margin-right: 10px;
  content: '';
  background-image: url("../../images/icons/svg-icons.svg#chevron-blue");
  background-repeat: no-repeat;
  background-size: 20px 20px;
  background-position: center;
}

/* -----------------------------------------------------------------------------
  Component: Admonitions

  Style for the different admonitions: note, warning, etc.
----------------------------------------------------------------------------- */
#central-column .admonition {
  padding: 0 30px 1px;
  background-color: #F9FCFF;
  border-radius: 10px;
  margin-bottom: 2rem;
}

#central-column .admonition .admonition-title {
  border-bottom: 1px solid #b7c6d7;
  margin-left: -8px;
  margin-right: -8px;
  padding: 16px 10px 10px 42px;
  font-weight: 700;
  font-size: 18px;
  position: relative;
  line-height: 28px;
}

#central-column .admonition .admonition-title::after {
  vertical-align: middle;
  content: '';
  display: inline-block;
  width: 1.5rem;
  height: 1.5rem;
  position: absolute;
  left: 8px;
  bottom: 10px;
}

#central-column .admonition.note .admonition-title {
  color: #256bd1;
}

#central-column .admonition.note .admonition-title::after {
  background: url("../../images/icons/svg-icons.svg#circle-check");
}

#central-column .admonition.warning .admonition-title {
  color: #B35621;
}

#central-column .admonition.warning .admonition-title::after {
  background: url("../../images/icons/svg-icons.svg#circle-exclamation-orange");
}

#central-column .admonition.attention .admonition-title {
  color: #256bd1;
}

#central-column .admonition.attention .admonition-title::after {
  background: url("../../images/icons/svg-icons.svg#circle-bell");
}

#central-column .admonition.caution .admonition-title {
  color: #B35621;
}

#central-column .admonition.caution .admonition-title::after {
  background: url("../../images/icons/svg-icons.svg#circle-exclamation-orange");
}

#central-column .admonition.danger .admonition-title {
  color: #bd271e;
}

#central-column .admonition.danger .admonition-title::after {
  background: url("../../images/icons/svg-icons.svg#circle-exclamation-red");
}

#central-column .admonition.error .admonition-title {
  color: #bd271e;
}

#central-column .admonition.error .admonition-title::after {
  background: url("../../images/icons/svg-icons.svg#circle-x");
}

#central-column .admonition.hint .admonition-title {
  color: #207D82;
}

#central-column .admonition.hint .admonition-title::after {
  background: url("../../images/icons/svg-icons.svg#circle-bulb");
}

#central-column .admonition.important .admonition-title {
  color: #256bd1;
}

#central-column .admonition.important .admonition-title::after {
  background: url("../../images/icons/svg-icons.svg#circle-exclamation-blue");
}

#central-column .admonition.tip .admonition-title {
  color: #207D82;
}

#central-column .admonition.tip .admonition-title::after {
  background: url("../../images/icons/svg-icons.svg#circle-bulb");
}

#central-column .admonition.admonition-generic-admonition .admonition-title {
  color: #000;
  padding-left: 10px;
}

#central-column .admonition.admonition-generic-admonition .admonition-title::after {
  content: none;
}

/* -----------------------------------------------------------------------------
  Component: Breadcrumbs

  Style for the breadcrumbs present in the content area.
----------------------------------------------------------------------------- */
#breadcrumbs {
  position: fixed;
  top: 77px;
  display: inline-flex;
  flex-direction: row;
  align-items: flex-end;
  width: calc(100% - 385px - 290px);
  height: 60px;
  padding: 5px 15px 5px 30px;
  margin: 0;
  list-style: none;
  background-color: #fff;
  z-index: 99;
}

#breadcrumbs .breadcrumb-separator::after {
  display: inline-block;
  margin: 0 15px;
  content: "/";
  color: #292929;
  font-size: 1.1rem;
}

/* -----------------------------------------------------------------------------
  Component: Code-blocks

  Style for the code-blocks and adjustment of the pygments.
----------------------------------------------------------------------------- */
main code,
main kbd,
main pre,
main samp {
  font-family: "Fira Mono", monospace;
}

.literal-block-wrapper.docutils.container {
  max-width: 100%;
  padding-left: 0;
  padding-right: 0;
}

.literal-block-wrapper.docutils.container .code-block-caption {
  display: flex;
  height: 48px;
  align-items: center;
  border: 2px solid #e2eefe;
  padding: 5px 10px;
  justify-content: space-between;
  margin-top: 1rem;
}

.literal-block-wrapper.docutils.container .code-block-caption .headerlink {
  text-decoration: none;
  line-height: 1;
}

.literal-block-wrapper.docutils.container .code-block-caption .headerlink::after {
  vertical-align: middle;
  content: '\f0c1';
  display: inline-block;
  width: 1rem;
  height: 1rem;
  color: #545f73;
  font: normal bold normal 0.95rem/1 FontAwesome;
  text-align: right;
  opacity: 0;
  transition: opacity ease-in .1s;
}

.literal-block-wrapper.docutils.container .code-block-caption:hover .headerlink::after {
  opacity: 1;
  transition: opacity ease-in .1s;
}

[class*="highlight-"] {
  max-width: 100%;
  margin-bottom: 1rem;
  position: relative;
}

[class*="highlight-"].output.highlight-console .output-title {
  height: 40px;
  display: flex;
  align-items: center;
  background-color: #ebf2fb;
  border-radius: 10px 10px 0 0;
  font-weight: 700;
  width: 100%;
}

[class*="highlight-"].output.highlight-console .output-title::before {
  content: '';
  height: 40px;
  width: 40px;
  background-image: url("../../images/icons/svg-icons.svg#chevron-blue");
  background-repeat: no-repeat;
  background-size: 18px 18px;
  background-position: center;
}

[class*="highlight-"] .highlighttable {
  background-color: #F8FAFD;
  table-layout: fixed;
  width: 100%;
}

[class*="highlight-"] .highlighttable .highlight {
  position: unset;
  overflow-x: auto;
  max-width: 100%;
  padding-left: 0;
  padding-right: 0;
  padding-bottom: 20px;
}

[class*="highlight-"] td.code pre {
  padding: 0 20px;
  overflow: visible;
  position: relative;
  z-index: 6;
}

[class*="highlight-"] .linenodiv {
  display: inline-flex;
  border-right: 1px solid #e9e9e9;
  position: absolute;
  top: 20px;
  z-index: 10;
  width: 50px;
  justify-content: center;
  user-select: none;
}

[class*="highlight-"] .linenos {
  width: 48px;
}

[class*="highlight-"] .highlight {
  background-color: #F8FAFD;
  font-family: "Fira Mono", monospace;
  color: #000;
  position: relative;
  padding: 20px 0 0;
  display: flex;
  overflow-x: auto;
  z-index: 0;
}

[class*="highlight-"] .highlight pre {
  margin: 0;
  position: unset;
  padding: 0 20px 20px;
}

[class*="highlight-"] .highlight .hll {
  display: block;
}

[class*="highlight-"] .highlight .hll::before {
  background-color: #e2eefe;
  position: absolute;
  left: 0;
  right: 0;
  height: 23px;
  display: block;
  content: '';
  z-index: -20;
}

[class*="highlight-"] .highlight .hll > span {
  position: relative;
  z-index: 25;
}

[class*="highlight-"] .highlight .k,
[class*="highlight-"] .highlight .kn {
  color: #000;
  font-weight: 700;
}

[class*="highlight-"] .highlight .c,
[class*="highlight-"] .highlight .c1,
[class*="highlight-"] .highlight .cp,
[class*="highlight-"] .highlight .ch,
[class*="highlight-"] .highlight .go {
  color: #83720a;
}

[class*="highlight-"] .highlight .m,
[class*="highlight-"] .highlight .mi {
  color: #0e4a9f;
}

[class*="highlight-"] .highlight .s,
[class*="highlight-"] .highlight .s1,
[class*="highlight-"] .highlight .s2,
[class*="highlight-"] .highlight .sb,
[class*="highlight-"] .highlight .se,
[class*="highlight-"] .highlight .sh,
[class*="highlight-"] .highlight .si {
  color: #bd3d9d;
}

[class*="highlight-"] .highlight .vm,
[class*="highlight-"] .highlight .n,
[class*="highlight-"] .highlight .nb,
[class*="highlight-"] .highlight .bp,
[class*="highlight-"] .highlight .nf {
  color: #256bd1;
}

[class*="highlight-"] .highlight .nt,
[class*="highlight-"] .highlight .na,
[class*="highlight-"] .highlight .no {
  color: #7c3db2;
}

.highlight-default .k,
.highlight-default .kn {
  font-weight: 700;
}

.highlight-default .c,
.highlight-default .c1,
.highlight-default .cp,
.highlight-default .ch,
.highlight-default .go {
  color: #83720a;
}

.highlight-default .m,
.highlight-default .mi {
  color: #0e4a9f;
}

.highlight-default .s,
.highlight-default .s1,
.highlight-default .s2,
.highlight-default .sb,
.highlight-default .se,
.highlight-default .sh,
.highlight-default .si {
  color: #bd3d9d;
}

.highlight-default .vm,
.highlight-default .n,
.highlight-default .nb,
.highlight-default .bp,
.highlight-default .nf {
  color: #256bd1;
}

.highlight-default .nt,
.highlight-default .na,
.highlight-default .no {
  color: #7c3db2;
}

/* -----------------------------------------------------------------------------
  Component: Customization for tabs

  Style adjustments for the elements created by sphinx-tabs.
----------------------------------------------------------------------------- */
#central-column .sphinx-tabs .menu p {
  margin-bottom: 0;
}

#central-column .sphinx-tabs .bottom {
  border: 1px solid #256bd1;
}

#central-column .sphinx-tabs p {
  font-family: "Manrope", sans-serif;
}

.sphinx-tabs {
  padding: 0;
  margin-bottom: 1rem;
}

.sphinx-tabs .docutils.container {
  padding-left: 0;
  padding-right: 0;
  width: auto;
  font-family: "Manrope", sans-serif;
}

.sphinx-tabs .docutils.container.menu {
  padding: 0;
  border: none;
  position: relative;
  z-index: 1;
}

.sphinx-tabs .docutils.container.bottom {
  padding-left: 15px;
  padding-right: 15px;
  margin-bottom: 0;
  border-radius: 10px;
}

.sphinx-tabs .docutils.container .item {
  margin: 0;
  background-color: #ebf2fb;
  font-weight: 400;
  border-radius: 10px 10px 0 0 !important;
  padding: 1.42857em calc(1.42857em + .15%);
}

.sphinx-tabs .docutils.container .item.active {
  border-color: #256bd1;
  background-color: #fff;
  color: #256bd1;
  font-weight: 700;
  padding: 1.42857em;
  border-radius: 10px 10px 0 0 !important;
}

.sphinx-tabs .docutils.container .item.active:hover {
  background-color: #fff;
}

.sphinx-tabs .docutils.container .item.active:first-child ::after {
  /* This hides the fact that the element .bottom has all corners round */
  content: '';
  display: block;
  position: absolute;
  bottom: -8px;
  left: -1px;
  width: 10px;
  height: 10px;
  background: #fff;
  border-left: 1px solid #256bd1;
  pointer-events: none;
}

.sphinx-tabs .docutils.container .item:hover {
  letter-spacing: 0;
  background-color: #ebf2fb;
  padding: 1.42857em;
}

.sphinx-tabs .docutils.container .item:hover * {
  font-weight: 700;
}

/* -----------------------------------------------------------------------------
  Component: Customization for lightbox

  Style adjustments for the elements created by our custom extension:
  wazuh_doc_images.
----------------------------------------------------------------------------- */
/* -----------------------------------------------------------------------------
  Component: Link boxes

  Style for the element containing the link boxes with logo images (agent-os).
----------------------------------------------------------------------------- */
.link-boxes-group {
  display: flex;
  display: grid;
  justify-content: space-between;
  grid-template-columns: repeat(3, 1fr);
